* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

body {
  overflow-x: hidden;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 3rem 0;
  color: #4d4d4d;
  font-weight: 600;
}

.title {
  font-size: 3rem;
  font-weight: 600;
  text-transform: uppercase;
}

.description {
  width: 80%;
  margin: 1.5rem 2rem 0 2rem;
  padding: 0.25rem 1rem;
  padding-top: 0;
  border: 0 solid #000;
  border-bottom-width: 2px;
  text-align: center;
  font-size: 1.55rem;
  font-weight: 600;
}

.subContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 30rem;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
}

.taskList {
  position: absolute;
  top: 0;
  left: auto;
  width: 95%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 28.75rem;
  margin-top: 1rem;
  padding: 0.5rem 0;
  padding-left: 6px;
  border: 1px solid #cccccc;
  box-shadow: 2px 2px 6px #555;
  overflow: scroll;
  overflow-x: hidden;
  transition: 0.5s;
  background-color: #fff;
}

.taskCountText {
  margin-top: 0.75rem;
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-thumb {
  background-color: #999;
}

@media only screen and (min-width: 600px) {
  .container {
    padding: 2.5rem;
  }

  .description {
    width: auto;
    margin-top: 1.25rem;
  }

  .mobileNewLine {
    display: none;
  }

  .subContainer {
    height: 30rem;
    width: 24rem;
  }

  .subContainer.open {
    width: 39rem;
  }

  .taskList {
    width: 22rem;
    left: 2.5%;
  }

  .taskList.slideLeft {
    left: 0;
  }

  .taskCountText {
    font-size: 1.5rem;
  }
}

@media only screen and (min-width: 800px) {
  .subContainer {
    width: 32rem;
  }

  .subContainer.open {
    width: 47rem;
  }

  .taskList {
    width: 30rem;
  }
}

@media only screen and (min-width: 1200px) {
  .subContainer {
    width: 42rem;
  }

  .subContainer.open {
    width: 62rem;
  }

  .taskList {
    width: 40rem;
  }
}

